@media screen and (max-width: 550px) {
	main {
		section {
			margin: 0 10px;
		}
		
	}

	q.lorebook {
		width: 90%;
	}


	section.bio {
		figure {
			float: none;
			width: auto;
		}
	}

	div#flex_container {
		flex-direction: column;
	}
	div.flex_column {
		width: 100%;
	}


	div.link_to_character {
		flex-direction: column;	
		margin: 4% 0;

		img {
			width: 100%;
			height: 200px;
		}

		div.text {
			width: 90%;
			text-align: center;

			h3 {
				text-align: center;
			}
		}
	}


	img#alice {
		width: 75%;
	}
	img#jane {
		width: 95%;
	}
	img#pandemonica, img#willow {
		width: 80%;
	}

	.image_table_row {
		margin: 0 10px;

		img {
			border-radius: 10px;
			padding: 5px;
		}

		p {
			width: 100%;
		}
	}

	div#alice_official_skins, div#alice_dlc_skins {
		.image_table_row {
			width: 40%;

			img {
				width: 100%;
			}
		}	
	}

	div#willow_skins {
		.image_table_row {
			width: 40%;

			img {
				width: 100%;
			}
		}
	}


	img.image_fullscreen.willow {
		padding: 3%;
		margin-top: 58%;
		width: 70%;
	}
	img.image_fullscreen.alice {
		padding: 3%;
		margin-top: 18%;
		width: 60%;
	}
}