@media screen and (max-width: 550px) {
	div#gallery_switches {
		display: flex;
		flex-direction: column;

		span {
			margin: 10px 0;
		}
	}


	div.image_container {
		height: 25vh;
		padding: 5px;

		img {
			width: 100%;
		}
	}


	#prev_photo, #next_photo {
		width: 10vw;
		height: 50vh;
		font-size: 2em;
	}
	#close_button > img {
		img {
			width: 24px;
			height: 24px;
		}
	}

	img.image_fullscreen {
		width: 75vw;
		height: 55vh;
		object-fit: contain;
		margin-top: 43%;
		padding: 5px;
	}

}